<script>
    import axios from "axios";
    // import GoodsDetail from '@/components/goods/GoodsDetail';
    import GoodCard from '@/components/goods/GoodCard'
    export default {
        name: 'goods',
        props: {
            goods: Object,
        },
        components: {
            // GoodsDetail,
            GoodCard,
        },
        mounted() {
            this.get_bgdinfo();
        },
        data() {
            return {
                // 商品信息
                bgd: {},
                // 商品列表
                bgd_PRDLIST: [],
                bgd_good: {},
            }
        },
        methods: {
            get_bgdinfo() {
                let __this = this;
                axios.get("/erp/bgd.json").then(
                    (response) => {
                        console.log(response);
                        __this.bgd = response["data"];
                        __this.bgd_PRDLIST = response["data"]['PRDLIST']
                    },
                    (error) => {
                        console.log(error);
                        alert(error);
                    }
                );
            },
        },
    }
</script>

