<template>
<div class="hello">
    <van-nav-bar title="Vant实践 - https://vant-contrib.gitee.io/vant/v3/#/zh-CN/home" left-text="返回" right-text="搜索" left-arrow @click-left="onClickLeft" @click-right="onClickRight" />
    <el-card>
        <h3>选择单个日期</h3>
        <p>下面演示了结合单元格来使用日历组件的用法，日期选择完成后会触发 confirm 事件。</p>
        <van-cell title="选择单个日期" :value="date" @click="show = true" class="chosedate"/>
        <van-calendar v-model:show="show" @confirm="onConfirm" />
    </el-card>
</div>
</template>

<script>
import {
    Toast
} from 'vant';
import {
    ref
} from 'vue';

export default {
    setup() {
        const date = ref('');
        const show = ref(false);

        const formatDate = (date) => `${date.getMonth() + 1}/${date.getDate()}`;
        const onConfirm = (value) => {
            show.value = false;
            date.value = formatDate(value);
        };

        return {
            date,
            show,
            onConfirm,
        };
    },
    name: 'HelloWorld',
    props: {
        msg: String
    },
    methods: {
        onClickLeft() {
            this.$router.push('/Note')
        },
        onClickRight() {
            Toast.loading({
                message: '加载中...',
                forbidClick: true,
                loadingType: 'spinner',
            });
        }
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
.hello {
    text-align: left;
}
.chosedate{
    border: 1px solid black;
    border-radius: 5px;
}
</style>
