import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
// element-plus@1.1.0-beta.12 引入
import ElementPlus from 'element-plus';
import 'element-plus/dist/index.css';
// Vant
// import { createApp } from 'vue';
import Vant from 'vant';
import 'vant/lib/index.css';
import {
    Calendar
} from 'vant';
// vue-masonry@0.13.0
import {
    VueMasonryPlugin
} from 'vue-masonry/src/masonry-vue3.plugin';


createApp(App)
// 注意use要在mount前
.use(VueMasonryPlugin)
.use(router)
.use(Vant)
.use(Calendar)
.use(ElementPlus)
.mount('#app')
