<template>
<div class="hello" @click="alertFunc(title)">
    <el-row>
        <el-col :span="24">
            <i :class="icon_url" class="icon"></i>
        </el-col>
    </el-row>
    <el-row>
        <el-col :span="24">
            <span class="title">{{title}}</span>
        </el-col>
    </el-row>
</div>
</template>

<script>
export default {
    name: 'Button_up_down',
    props: {
        icon_url: String,
        title: String
    },
    methods: {
        alertFunc(msg) {
            this.$alert(msg, msg, {
                confirmButtonText: '确定',
                callback: action => {
                    this.$message({
                        type: 'info',
                        message: `action: ${ action }`
                    });
                }
            });
        }
    },
}
</script>

