<template>
<div class="cardinfohome">
    <h3>自定义组件-展示卡片信息</h3><br>    
    <!-- 组件传值 -->
    <!-- msg前面不带：冒号 说明是不可变，cardinfo前面加：冒号 说明是可变的 -->
    <span>*msg前面不带：冒号 说明是不可变，cardinfo前面加：冒号 说明是可变的 - 详情请查看源码</span>
    <br><br><br>
    <span>方案一</span>
    <!-- 方案一 -->
    <CardInfoDetail msg="CardInfoHome.vue->CardInfoDetail.vue" :cardinfo="cardinfo"></CardInfoDetail>
    <span>方案二</span>
    <!-- 方案二 -->
    <CardInfoDetail :msg="msg" :cardinfo="cardinfo"></CardInfoDetail>
</div>
</template>

<script>
import axios from "axios";
// 引用
import CardInfoDetail from '@/components/cardinfo/CardInfoDetail'

export default {
    name: 'cardinfohome',
    props: {
        // msg: String
    },
    components: {
        // 挂载
        CardInfoDetail,
    },
    data() {
        return {
            msg: 'CardInfoHome.vue->CardInfoDetail.vue',
            // 获取卡片信息
            cardinfo: {},
        }
    },
    mounted() {
        this.get_cardinfo();
    },
    methods: {
        get_cardinfo() {
            let __this = this;
            axios.get("/card/card.json").then(
                (response) => {
                    console.log(response);
                    __this.cardinfo = response["data"];
                },
                (error) => {
                    console.log(error);
                    alert(error);
                }
            );
        },
    },
}
</script>

