<!-- vueelement -->
<template>
<div class='app'>
    <el-card>
        <van-nav-bar title="Note" left-text="返回" left-arrow @click-left="back" @click-right="onClickRight" />
    </el-card>
    <h3 style="text-align:left;color:red;">Vue组件库整理-更新中...</h3>
    <Collapse v-for="item in vueinfo" :key="item.id" class="content">
        <el-card>
            <h3><span> {{ item.id }} - {{ item.name }}</span></h3>
            <div class="card" style="text-align:left;">
                <Panel>
                    <div>
                        <a class="linkurl" style="color:blue;" @click="See(item.address)">地址:{{ item.address }}</a>
                        <p>描述:{{ item.desc }}</p>
                        <p>优点:{{ item.advantage }}</p>
                        <p>缺点:{{ item.disadvantage }}</p>
                    </div>
                </Panel>
            </div>
        </el-card>
    </Collapse>
</div>
</template>

<script>
import axios from "axios";
// 导入的其他文件 例如：import moduleName from 'modulePath';
export default {
    // import所引入的组件注册
    components: {

    },

    name: '',
    data() {
        return {
            msg: '属性',
            vueinfo: []
        }
    },

    // 监听属性
    computed: {

    },

    // 监控data中的数据变化
    watch: {

    },

    // 方法集合
    methods: {
        back() {
            this.$router.go(-1)
        },
        getVueElementJson() {
            const url = '/learn/vueelement.json'
            // alert('进入就调用方法: getMainLeftJson()  url=' + url)
            // axios.get({
            //     url: url
            // }).then((res) => {
            //     var obj = JSON.parse(JSON.stringify(res.data))
            //     this.vueinfo = obj.VueElement
            // }).catch((err) => {
            //     console.log(err)
            //     alert(err)
            // })
            axios.get(url).then(
                (response) => {
                    var data_obj = response.data
                    this.vueinfo = data_obj.VueElement

                    var data_str = JSON.stringify(data_obj)
                    console.log(data_str)
                },
                (error) => {
                    console.log(error);
                    alert(error);
                }
            );
        },
        See(e) {
            // alert(e)
            window.location.href = e
        }
    },

    // 生命周期 - 组件实例刚被创建
    beforeCreate() {

    },
    // 创建完成 访问当前this实例
    created() {

    },
    // 挂载之前
    beforeMount() {

    },
    // 挂载完成 访问DOM元素
    mounted() {
        this.getVueElementJson()
    },
    // 更新之前
    beforeUpdate() {

    },
    // 更新之后
    updated() {

    },
    // for keep-alive 缓存功能，组件被激活时调用
    activated() {

    },
    // for keep-alive 组件被移除时调用
    deactivated() {

    },
    // 组件销毁之前调用
    beforeUnmount() {

    },
    // 组件销毁之后调用
    unmounted() {

    }
}
</script>

