<template>
    <div class="vuethreelearn">
        <h3>{{title}}</h3><br>
        <el-card>
            <div class="header"></div>
            <div class="content"></div>
        </el-card>

        <el-card>
            <div class="demo-collapse">
                <el-collapse v-model="activeNames" @change="handleChange" v-for="(item,index) in data_aside"
                    :key="index">
                    <el-collapse-item :title=item.title :name=index>
                        <el-row :gutter="24">
                            <el-col :span="6">path:{{item.path}}</el-col>
                            <el-col :span="18"></el-col>
                        </el-row>
                        <el-row :gutter="24">
                            <el-col :span="24">描述:{{item.desc}}</el-col>
                        </el-row>
                        <el-row :gutter="24">
                            <el-col :span="18"></el-col>
                            <el-col :span="6">time:{{item.time}}</el-col>
                        </el-row>
                        <el-row :gutter="24">
                            <el-col :span="24">
                                <!-- to前面不带：冒号 说明是不可变，to前面加：冒号 说明是可变的 -->
                                <router-link :to=item.path>
                                    <ul>
                                        <li>点击链接跳转到:{{item.path}}</li>
                                    </ul>
                                </router-link>
                            </el-col>
                        </el-row>
                    </el-collapse-item>
                </el-collapse>
            </div>
        </el-card>

    </div>
</template>

<script>
    import axios from "axios";

    export default {
        name: 'vuethreelearn',
        props: {
            // msg: String
        },
        components: {
            // 挂载
            //CardInfoDetail,
        },
        data() {
            return {
                title: '菜单列表',
                // 获取卡片信息
                cardinfo: {},
                // 左侧边栏数据
                data_aside: [],
            }
        },
        mounted() {
            this.get_cardinfo();
            this.get_data_aside();
        },
        methods: {
            get_cardinfo() {
                let __this = this;
                axios.get("/card/card.json").then(
                    (response) => {
                        console.log(response);
                        __this.cardinfo = response["data"];
                    },
                    (error) => {
                        console.log(error);
                        alert(error);
                    }
                );
            },
            get_data_aside() {
                let __this = this;
                axios.get("/aside/aside.json").then(
                    (response) => {
                        var data_obj = response["data"]
                        var data_str = JSON.stringify(data_obj)
                        console.log(data_str)
                        __this.data_aside = data_obj;
                    },
                    (error) => {
                        console.log(error);
                        alert(error);
                    }
                );
            },
        },
    }
</script>

