<template>
<div class="hello">
    <el-card>
        <h3>localStorage 存、读、清理操作</h3>
        <div class="good_data">
            {{good}}
        </div>
    </el-card>
    <el-card>
        <div class="good_op">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-button type="primary" @click="save(good)">存储数据</el-button>
                </el-col>
                <el-col :span="8">
                    <el-button type="success" @click="get('good')">取出数据</el-button>
                </el-col>
                <el-col :span="8">
                    <el-button type="danger" @click="del('good')">清除数据</el-button>
                </el-col>
            </el-row>
        </div>
    </el-card>
    <el-card>
        <h3>vue JS对象赋值</h3>
        <el-row :gutter="10">
            <el-col :span="5">
                <el-button type="primary" @click="newObj('加菲猫','male',28)">对象赋值</el-button>
            </el-col>
            <el-col :span="5">
                <el-button type="success" @click="getObj('cat')">对象取值</el-button>
            </el-col>
            <el-col :span="14">
                <el-button type="danger">对象置null-其实没有必要,只要记得清理存储在localStorage里数据就行</el-button>
            </el-col>
        </el-row>
    </el-card>
    <el-card>
        <h3>vue.js怎么获取input里面的值</h3>
        <h4>一、使用$.ref来标识元素，代码:</h4>
        <el-card>
            <div class="">
                <p class="up-title">补充元素</p>
                <textarea name="" id="" cols="60" rows="5" placeholder="请在此输入异常情况的补充说明" ref="upTitleArea"></textarea>
                <br><br>
                <el-button type="success" @click="submint">实时上传</el-button>
            </div>
        </el-card>

        <h4>二、使用v-model双向绑定</h4>
        <el-card>
            <div class="">
                <p class="up-title">补充元素</p>
                <textarea name="" id="" cols="60" rows="5" placeholder="请在此输入异常情况的补充说明" v-model="upTitleArea_v_model"></textarea>
                <br><br>
                <el-button type="success" @click="submint_v_model">实时上传</el-button>
            </div>
        </el-card>
        <h4>用户登录模块</h4>
        <el-card>
            <el-row type="flex" class="row-bg" justify="center">
                <el-col :span="12">
                    <div class="grid-content bg-purple-light">
                        <el-input v-model="account_input" placeholder="请输入账户名"></el-input>
                    </div>
                </el-col>
            </el-row>
            <br><br>
            <el-row type="flex" class="row-bg" justify="center">
                <el-col :span="12">
                    <div class="grid-content bg-purple-light">
                        <el-input v-model="pwd_input" placeholder="请输入密码" show-password></el-input>
                    </div>
                </el-col>
            </el-row>
            <br><br>
            <el-row type="flex" class="row-bg" justify="center">
                <el-col :span="12">
                    <div class="grid-content bg-purple-light">
                        <el-button type="success" @click="login">登录</el-button>
                    </div>
                </el-col>
            </el-row>
        </el-card>
    </el-card>
</div>
</template>

<script>
export default {
    name: 'LocalStorage',
    props: {
        msg: String
    },
    data() {
        return {
            good: {
                "icon": "el-icon-phone",
                "PRDINDR": "买一送一",
                "PRDTYPE": "食品",
                "PRDTYPENO": "0001",
                "PRDREMAIN": "",
                "PRDNM": "果冻",
                "PRDNO": "0001",
                "PRDPRC": "1.00"
            },
            upTitleAreaResult: null,
            upTitleArea_v_model: '',
            // 登录:账户、密码
            account_input: '',
            account_result: '',
            pwd_input: '',
            pwd_result: ''

        }
    },
    methods: {
        login() {
            this.account_result = this.account_input
            this.pwd_result = this.pwd_input
            var userinfo = {}
            userinfo['account'] = this.account_result
            userinfo['pwd'] = this.pwd_result
            var userinfoStr = JSON.stringify(userinfo)
            alert('用户登录信息:'+userinfoStr)
        },
        submint_v_model() {
            this.upTitleAreaResult = this.upTitleArea_v_model;
            alert('二、使用v-model双向绑定:' + this.upTitleAreaResult)
        },
        submint() {
            this.upTitleAreaResult = this.$refs.upTitleArea.value;
            alert('一、使用$.ref来标识元素:' + this.upTitleAreaResult)
        },
        newObj(name, sex, age) {
            var obj = {}
            obj['name'] = name
            obj['sex'] = sex
            obj['age'] = age
            // 对象转字符串
            var objStr = JSON.stringify(obj)
            alert('如下做cat localStorage存储:' + objStr)
            localStorage.setItem('cat', objStr)
        },
        getObj(key) {
            var catStr = localStorage.getItem(key)
            alert('localStorage取出cat 数据字符串:' + catStr)
            var catObj = JSON.parse(catStr)
            alert('cat name is:' + catObj.name)
        },
        save(data) {
            var tarStr = JSON.stringify(data)
            console.log('---save' + tarStr)
            localStorage.setItem('good', tarStr)

            this.$alert(tarStr, '存储数据', {
                confirmButtonText: '确定',
                callback: action => {
                    this.$message({
                        type: 'info',
                        message: `action: ${ action }`
                    });
                }
            });
        },
        get(key) {
            var goodStr = localStorage.getItem(key);
            if (goodStr === null) {
                var msg = 'key=' + key + ',数据为空;'

                this.$alert(msg, '温馨提示', {
                    confirmButtonText: '确定',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: ${ action }`
                        });
                    }
                });
            } else {
                console.log('---get' + goodStr)

                this.$alert(goodStr, '取出数据', {
                    confirmButtonText: '确定',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: ${ action }`
                        });
                    }
                });
            }
        },
        del(key) {
            localStorage.removeItem(key)
            var msg = 'key=' + key + ',数据清理成功;'

            this.$alert(msg, '清除数据', {
                confirmButtonText: '确定',
                callback: action => {
                    this.$message({
                        type: 'info',
                        message: `action: ${ action }`
                    });
                }
            });
        },
    },
}
</script>

